
import { defineComponent, onMounted, reactive, ref } from 'vue'
import homeFooter from "@/components/footer.vue"
import { useRouter } from "vue-router"
import { getCategoryList } from "@/api/index"

export default defineComponent({
  name: 'category',
  components: {
    homeFooter
  },
  setup(props, context){
    const router = useRouter()
    const state = reactive({
      value: "",
      // categoryListLeft: [{ text: '分组 1', id: 0 }, { text: '分组 2', id: 1 }],
      categoryListLeft: [{}],
      categoryListRight: [{}],
      activeId: 1,
      activeIndex: 0
    })
    const onSearch = () => {
      console.log("666", state.value)
    }

    // 生命周期获取数据
    onMounted(() => {
      getCategoryList().then(res => {
        // console.log("----888", res)
        if(res.code == 200){
          // state.categoryListLeft = res.data
          state.categoryListLeft = []
          res.data.map((item: any, index: any): any => {
            state.categoryListLeft.push({id: item.id, text: item.name, list: item.list })
          })
        }
      })
    })

    // 进入搜索页面
    const handleEnterSearch = () => {
      router.push("/search")
    }

    // 进入详情页
    const handleEnterDetail = (item: any, index: number) => {
      console.log("item-0000", item)
      router.push({
        path: "/categoryDetail",
        query: {
          id: item.id,
          pid: item.pid
        }
      })
    }

    return {
      state,
      onSearch,
      handleEnterSearch,
      handleEnterDetail
    }
  }
})
